<template>
    <div id="container01">
        <div id="filter">
            <h5>FILTRADO POR</h5>
            <!--<div>X BORRAR FILTROS</div>-->
            <button type="button" class="btn btn-danger"  :class=" deleteFilters ? 'deleteFilters-active' : 'deleteFilters-no-active' " @click="displayDeleteFilters">X Borrar Filtros</button>
            
            <form id="filters-box">
                
                <!--
                
                <fieldset class="filter-set">
                    <h4>Colores</h4>
                    <div v-for="(cor,index) in cor_filters" :key="index">
                        <div class="form-check">
                            <input @click="getData" class="form-check-input" name="cor_selected" :value="[cor,index]" type="checkbox" id="check_cor"> 
                            <label class="form-check-label" for="check_cor">{{cor}}</label>
                        </div>
                    </div>
                </fieldset>

                -->

                <fieldset class="filter-set">
                    <h4>Marcas</h4>
                    <div v-for="(marca,index) in marca_filters" :key="index">
                        <div class="form-check">
                            <input @click="getData" class="form-check-input" name="marca_selected" :value="[marca,index]" type="checkbox" id="check_marca"> 
                            <label class="form-check-label" for="check_marca">{{marca}}</label>
                        </div>
                    </div>
                </fieldset>

                <!--
                <fieldset class="filter-set">
                    <h4>Precio</h4>
                </fieldset>
                -->

                <fieldset class="filter-set">
                    <h4>Tallas</h4>
                    <div v-for="(talla,index) in talla_filters" :key="index">
                        <div class="form-check">
                            <input @click="getData" class="form-check-input" name="talla_selected" :value="[talla,index]" type="checkbox" id="check_talla"> 
                            <label class="form-check-label" for="check_talla">{{talla}}</label>
                        </div>
                    </div>
                </fieldset>
                

            </form>

        </div>


        <div id="show-filter">
            
            <div :class=" polos_items.length === 0 ? 'no-items-found-active' : 'no-items-found-no-active' " >
                <img class="ficon no-items" src="../assets/icons/shopcar.svg" alt="shopcar icon">
                <p class="description-no-items">No se encontraron items</p>
            </div>

            <div :class=" polos_items.length > 0 ? 'show-catalog' : 'no-items-found-active' " >
                <div class="items" >
                    <div class="card" v-for="product in polos_items" v-bind:key="product.id" v-bind:virtualIndex="product.id">

                        <div class="item-div">
                            <a class="item-content" :href="'/items/'+product.id">
                                <div>
                                    <figure class="figure-item">
                                        <img class="image-item" v-bind:src="product.get_image" alt="">
                                    </figure>
                                    <div class="info">
                                        <p class="info-name">{{product.name_modelo}}</p>
                                        <p class="info-price">S/. {{product.price}}</p>
                                    </div>
                                </div>
                            </a>
                            <div class="item-opciones">
                                <a class="bton-special anchor" :href="'/try-on/'+product.id"><div>Probar</div></a>
                                <a class="anchor" :class="userSession ? 'favo-icon' : 'favo-no-icon'" @click="addFavorite(product.id)" href="#"><img :src="isFavorite.indexOf(product.id) > -1 ? changeIcon(true) : changeIcon(false)" class="ficon" ></a>
                            </div>
                        </div> 
                    </div> 
                </div>

                <nav class="paginationBar" aria-label="...">
                    <ul class="pagination pagination-md">
                        <li class="page-item" v-for="page in pages" :key="page" :class="{active: page == current_page}"><a class="page-link" @click="nextPage(page)" href="#">{{page}}</a></li>
                    </ul>
                </nav>
            </div>
            
        </div>


    </div>
</template>


<script>
import axios from 'axios'

export default{
    name: "MyCatalog",
    components: {
    },
    data(){
        return{
            isFavorite: [],
            polos_items: [],
            current_page: 1,
            pages: 1,
            isLoading: false,
            cor_filters: [],
            marca_filters: [],
            talla_filters: [],
            price_filters: [],
            deleteFilters: false,
            catalogo_url: "/api/catalogo/polos/?page=",
            filtro_url: "/api/catalogo/filter_polos/?page=",
            tmp_search_filter_url: "",
            filtro_url_tmp: "",
            searchFilter: false,
            itemFound: false,
            userSession: false,
        }
    },
    mounted(){
        this.checkUserSession()
        this.checkFavoritesStorage()
        this.isLoading = true;
        this.nextPage();
        this.getFilters();
    },
    methods: {
        nextPage(page,str_url=""){
            if(str_url.length == 0 && !this.searchFilter){
                this.isLoading = true;
                page = page || this.current_page;
                axios
                    .get(this.catalogo_url + page)
                    .then(response => {
                        this.polos = response.data.results;
                        console.log(this.polos);
                        this.pages = Math.ceil(response.data.count/6);
                        this.current_page = page;
                        this.polos_items = this.polos;
                    })
                    .catch(error => {
                        console.log(error);
                    })
                this.isLoading = false;
            }
            
            else if(str_url.length == 0 && this.searchFilter){
                page = page || this.current_page;
                axios
                    .get(this.filtro_url_tmp + page + this.tmp_search_filter_url)
                    .then(response => {
                        this.polos = response.data.results;
                        this.pages = Math.ceil(response.data.count/6);
                        this.current_page = page;
                        this.polos_items = this.polos;
                    })
                    .catch(error => {
                        console.log(error);
                    })
    
            }
            
            else{
                this.searchFilter = true;
                str_url = str_url.slice(1);
                var new_filtro_url = this.filtro_url.slice(0,-5);
                var tmp_url = str_url.split("&");
                new_filtro_url += tmp_url[0] + "&page=";
                str_url = str_url.slice(tmp_url[0].length); 

                this.tmp_search_filter_url = str_url;
                this.filtro_url_tmp = new_filtro_url;

                //console.log("nextPage"+str_url);

                this.current_page = 1;
                this.pages = 1;
                page = page || this.current_page;

                //console.log("nextPage"+this.filtro_url_tmp + page + this.tmp_search_filter_url);

                axios
                    .get(this.filtro_url_tmp + page + this.tmp_search_filter_url)
                    .then(response => {
                        this.polos = response.data.results;
                        this.pages = Math.ceil(response.data.count/6);
                        this.current_page = page;
                        this.polos_items = this.polos;
                    })
                    .catch(error => {
                        console.log(error);
                    })
             
            }

        },
        getFilters(){
            axios
                .get("/api/catalogo/filters/")
                .then(res => {
                    this.data = res.data;
                    this.cor_filters = this.data.colores;
                    this.marca_filters = this.data.marcas;
                    this.talla_filters = this.data.tallas;
                    this.price_filters = this.data.rango_precio;
                })
                .catch(error =>{
                    console.log(error)
                })
        },
        getData(){
            if(this.deleteFilters === false) this.deleteFilters = true;
            var checked = document.querySelectorAll('input:checked');
            if(checked.length == 0) {
                this.deleteFilters = false;

                this.searchFilter = false;
                this.nextPage(1);
            }

            const form = document.getElementById("filters-box");
            const filterData = new FormData(form);

            const sendFilters = { "list_colors": [] ,
                        "range_price": [] ,
                        "list_tallas": [] ,
                        "list_marcas": [] }

            for (var pair of filterData.entries()) {
                const check_item = pair[0].split("_");
                if(check_item[1] === "selected"){
                    const value_check = pair[1].split(",");
                    
                    if(check_item[0] === "cor"){
                        sendFilters["list_colors"].push(parseInt(value_check[1])+1)
                    }
                    else if(check_item[0] === "marca"){
                        sendFilters["list_marcas"].push(parseInt(value_check[1])+1)
                    }
                    else if(check_item[0] === "talla"){
                        sendFilters["list_tallas"].push(value_check[0])
                    }
                }
            }
            sendFilters["range_price"] = this.price_filters;
            var params = "?";

            for(const val in sendFilters){
                if(sendFilters[val].length > 0){
                    params += val + "=";
                    
                    for(const elem of sendFilters[val]){
                        params += elem + ",";
                    }
                    params = params.slice(0, -1);
                    params += "&";
                }
            }
            params = params.slice(0, -1);

            this.nextPage(1,params);
             
        },
        displayDeleteFilters(){
            var checked = document.querySelectorAll('input:checked');
            if (checked.length !== 0) {
                for(var i=0; i<checked.length;i++){
                    checked[i].checked = false;
                }
            }

            this.searchFilter = false;
            this.nextPage(1);

            this.deleteFilters = false;
        },
        addFavorite(id){
            
            //console.log(id);
            //console.log(this.isFavorite)
            
            if(this.isFavorite.indexOf(id) > -1) this.isFavorite.splice(this.isFavorite.indexOf(id),1)
            else this.isFavorite.push(id);

            //linking product to user

            if(this.isFavorite.length == 0) localStorage.removeItem("list_favorites");
            else localStorage.setItem("list_favorites",this.isFavorite);

            const fav = {
                "id_polo": null,
                "id_user": null
            }

            fav["id_polo"] = id;
            fav["id_user"] = localStorage.getItem("user_session")

            axios
                .post("/api/registerPoloFavorite/",fav)
                .then(response =>{
                    this.msg = response.data;
                    console.log(this.msg);
                })
                .catch(error => {
                    console.log(error)
                })


        },
        changeIcon(value){
            if(value === false){
                return require('../assets/icons/heart.svg');
            }else{
                return require('../assets/icons/heart-fill.svg');
            }
        },
        checkUserSession(){
            const activeUserSession = localStorage.getItem("user_session");
            if(activeUserSession != null){
                this.userSession = true;
            }
            else{
                this.activeSession = false;
            }
        },
        checkFavoritesStorage(){
            const checkFav = localStorage.getItem("list_favorites");
            const listFav = [];

            if(checkFav != null){
                const strFavList = checkFav.split(",");
                strFavList.forEach( id_fav => listFav.push(parseInt(id_fav)));
                listFav.forEach( item => this.isFavorite.push(item));
                //console.log(listFav);
            }
            
        },

    },
}
</script>

