    border-style: solid none none none;
    border-color: #2b375c;
    border-width: 2px;
    display:flex;
    justify-content: center;
    gap: 10px;
    align-items: center;
}

.material-icons{
    margin-top: 1rem;
    /*color: red;*/
}

a{
    text-decoration: none;
}

/*Item Card*/ 


/*Item Card*/ 

